import {Component, Inject} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams, ToastController, ActionSheetController} from 'ionic-angular';
import {Dish} from "../../shared/dish";
import {Comment} from "../../shared/comment";
import {FavoriteProvider} from "../../providers/favorite/favorite";
import {CommentPage} from "../comment/comment";
import {CommentPageModule} from "../comment/comment.module";

/**
 * Generated class for the DishdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-dishdetail',
    templateUrl: 'dishdetail.html',
})
export class DishdetailPage {
    dish: Dish;
    errMess: string;
    avgstars: string;
    numcomments: number;
    comment: string;
    favorite: boolean;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                @Inject('baseUrl') private baseUrl,
                private favoriteservice: FavoriteProvider,
                private toastCtrl: ToastController,
                public actionSheetCtrl: ActionSheetController,
                public modalCtrl: ModalController) {
        this.dish = navParams.get('dish');
        this.numcomments = this.dish.comments.length;
        this.favorite = this.favoriteservice.isFavorite(this.dish.id);
        let total = 0;
        this.dish.comments.forEach(comment => total += comment.rating);
        this.avgstars = (total / this.numcomments).toFixed(2);

    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad DishdetailPage');
    }

    addToFavorites() {
        console.log('Adding to Favorites', this.dish.id);
        this.favorite = this.favoriteservice.addFavorite(this.dish.id);
        this.toastCtrl.create({
            message: 'Dish ' + this.dish.name + ' added as favorite successfully',
            position: 'middle',
            duration: 3000
        }).present();
    }

    showCommentModal() {
        let modal = this.modalCtrl.create("CommentPage");
            modal.onDidDismiss(data=>{
                this.dish.comments.push(data);
            });
        modal.present();
    }

    showActionSheet(){
        let actionSheet = this.actionSheetCtrl.create({
            title: 'More',
            buttons: [
                {
                    text: 'Add to Favorites',
                    role: 'destructive',
                    handler: ()=> {
                        this.addToFavorites();
                    }
                },{
                    text: 'Add a Comment',
                    role: 'destructive',
                    handler: ()=> {
                        this.showCommentModal();

                    }
                },{
                    text: 'Cancel',
                    role: 'cancel',
                    handler: ()=> {
                        console.log('Cancel clicked');
                    }
                }
            ]
        });
        actionSheet.present();
    }
}



